export default {
	
	data() {
		return {
			emailNum:'',
			password:'',
			opcity:0.5,
			
		}
	},
	onLoad() {
		
	},
	onReady() {
		
	},
	methods: {
		backupMnemonic() {
			if(this.emailNum!=this.password){
				uni.showToast({
					title:"两次密码不一致",
					icon:"none"	
				})
			}else{
				uni.navigateTo({
					url: "backupMnemonic1"
				})
			}
			
		},
		change(e){
			console.log(e.detail.value.length)
			if (e.detail.value.length >= 3) {
				this.opcity = 1
			}else{
				this.opcity = 0.5
			}
		}
	}
}
